import { GetComponentProps, styled } from 'topwrite';
import { useEffect, useRef } from 'react';

const sequence = require('../lib/sequence-diagram.js');

export default function Sequence({ props: { value } }: GetComponentProps<'x-block'>) {

    const container = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (container.current) {
            container.current.innerHTML = '';

            try {
                const diagram = sequence.parse(value);

                diagram.drawSVG(container.current, {
                    theme: 'simple',
                    'font-family': 'inherit'
                });
            } catch (e) {
                console.error(e);
            }
        }
    }, [value]);

    return <Container ref={container} />;
}
const Container = styled.div`
  text-align: center;
`;
